.header {
  background-color: rgba(17, 24, 39, 0.8);
  backdrop-filter: blur(12px);
  position: sticky;
  top: 0;
  z-index: 50;
  border-bottom: 1px solid #374151;
}

.nav {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 4rem;
}

.logo {
  font-size: 1.5rem;
  font-weight: normal;
  color: white;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s;
}

.logo:hover {
  color: #3b82f6;
}

.nav-desktop {
  display: none;
  gap: 1.5rem;
}

@media (min-width: 768px) {
  .nav-desktop {
    display: flex;
  }
}

.nav-link {
  color: #d1d5db;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem 0;
  font-size: 1rem;
  transition: color 0.3s;
  position: relative;
}

.nav-link:hover {
  color: white;
}

.nav-link-active {
  color: #3b82f6;
  border-bottom: 2px solid #3b82f6;
}

.mobile-menu-button {
  display: block;
}

@media (min-width: 768px) {
  .mobile-menu-button {
    display: none;
  }
}

.menu-icon {
  color: #d1d5db;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.menu-icon:hover {
  color: white;
}

.icon {
  width: 1.5rem;
  height: 1.5rem;
}

.mobile-menu {
  background-color: #1f2937;
  display: block;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu-item {
  width: 100%;
  text-align: left;
  padding: 0.75rem 1rem;
  color: #d1d5db;
  background: none;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.mobile-menu-item:hover {
  background-color: #374151;
  color: white;
}

.mobile-menu-item-active {
  color: white;
  background-color: #2563eb;
}

/* remove underline everywhere in the header nav (logo, desktop, mobile) */
.header .nav a,
.header .mobile-menu a,
.header .logo {
  text-decoration: none;
}

/* keep it off on hover/focus/visited too */
.header .nav a:hover,
.header .nav a:focus,
.header .nav a:visited,
.header .mobile-menu a:hover,
.header .mobile-menu a:focus,
.header .mobile-menu a:visited,
.header .logo:hover,
.header .logo:focus {
  text-decoration: none;
}

/* make links behave like your old buttons */
.nav-link,
.mobile-menu-item,
.logo {
  display: inline-block; /* so the bottom border shows nicely */
}
