.page-container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 3rem 1rem;
}

@media (min-width: 640px) {
  .page-container {
    padding: 3rem 1.5rem;
  }
}

@media (min-width: 1024px) {
  .page-container {
    padding: 3rem 2rem;
  }
}

/* Hero Section */
.hero-section {
  text-align: center;
  padding: 4rem 0 6rem 0;
}

@media (min-width: 768px) {
  .hero-section {
    padding: 6rem 0;
  }
}

.hero-title {
  font-size: 2.25rem;
  font-weight: normal;
  margin-bottom: 1rem;
  line-height: 1.2;
}

@media (min-width: 768px) {
  .hero-title {
    font-size: 3.75rem;
  }
}

.hero-description {
  font-size: 1.125rem;
  color: #d1d5db;
  max-width: 48rem;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .hero-description {
    font-size: 1.25rem;
  }
}

/* Content Card */
.content-card {
  background-color: #1f2937;
  border-radius: 0.5rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  padding: 2rem;
  margin-bottom: 3rem;
}

@media (min-width: 768px) {
  .content-card {
    padding: 3rem;
  }
}

/* Section Title */
.section-title {
  font-size: 1.875rem;
  font-weight: normal;
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #3b82f6;
}

.page-title {
  font-size: 1.875rem;
  font-weight: normal;
  margin-bottom: 2rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #3b82f6;
}

@media (min-width: 768px) {
  .page-title {
    font-size: 2.25rem;
  }
}

/* Text Content */
.text-content {
  font-size: 1.125rem;
  color: #d1d5db;
  line-height: 1.75;
  margin-bottom: 1rem;
}

.text-content:last-child {
  margin-bottom: 0;
}

/* Research List */
.research-list {
  list-style-type: disc;
  list-style-position: outside;
  padding-left: 1.5rem;
  margin-top: 2rem;
}

.research-item {
  font-size: 1.25rem;
  color: #d1d5db;
  line-height: 1.75;
  margin-bottom: 1rem;
}

/* Resource Item */
.resource-item {
  background-color: rgba(17, 24, 39, 0.5);
  border-radius: 0.5rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  transition: transform 0.3s, box-shadow 0.3s;
}

.resource-item:hover {
  transform: scale(1.02);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

.resource-title {
  font-size: 1.25rem;
  font-weight: normal;
  color: #3b82f6;
  margin-bottom: 0.5rem;
  cursor: pointer;
}

.resource-title:hover {
  text-decoration: underline;
}

.resource-description {
  color: #d1d5db;
}

/* Team Grid */
.team-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2.5rem;
  margin-top: 3rem;
}

@media (min-width: 640px) {
  .team-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .team-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Team Member Card */
.team-member {
  background-color: rgba(17, 24, 39, 0.5);
  border-radius: 0.5rem;
  padding: 1.5rem;
  text-align: center;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.team-member:hover {
  transform: scale(1.05);
}

.team-photo {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 4px solid #3b82f6;
  object-fit: cover;
}

.team-photo-placeholder {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 4px solid #3b82f6;
  background-color: #374151;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #9ca3af;
  font-size: 0.875rem;
}

.team-name {
  font-size: 1.25rem;
  font-weight: normal;
  margin-bottom: 0.25rem;
}

.team-role {
  color: #3b82f6;
  margin-bottom: 0.5rem;
}

.team-description {
  color: #d1d5db;
  font-size: 0.875rem;
  margin-bottom: 1rem;
}

.team-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.team-link {
  color: #9ca3af;
  text-decoration: none;
  transition: color 0.3s;
}

.team-link:hover {
  color: #3b82f6;
}

/* Shared subsection heading (used across pages) */
.subsection-title {
  font-size: 1.25rem;
  font-weight: normal;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color: #e5e7eb;
}

/* Shared accordion styles (used on Data and About pages) */
.accordion {
  background-color: rgba(17, 24, 39, 0.35);
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 0.5rem;
  margin-top: 0.75rem;
  margin-bottom: 0.5rem;
  overflow: hidden;
}

.accordion-summary {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  list-style: none;
  cursor: pointer;
  padding: 0.5rem 0.75rem;
  font-weight: normal;
  color: #e5e7eb;
}

.accordion-summary::-webkit-details-marker { display: none; }

.accordion-summary::before {
  content: '▸';
  color: #9ca3af;
  transition: transform 0.2s ease;
}

details[open] .accordion-summary::before { transform: rotate(90deg); }

.accordion-summary:hover { background-color: rgba(55, 65, 81, 0.5); }

.accordion-summary:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.4);
  border-radius: 0.375rem;
}

.accordion-content { padding: 0 0.75rem 0.75rem; }
